import classes from './Checkout.module.css';
import { useRef, useState } from 'react';

const isEmpty = value => value.trim() === "";
const isFiveChar = value => value.trim().length !== 5;

const Checkout = (props) => {

    const [formInputcValidity, setFormInputcValidity] = useState({
        name: true,
        street: true,
        city: true,
        postalCode: true
    });

    const nameInputRef = useRef();
    const streetInputRef = useRef();
    const postalCodeInputRef = useRef();
    const cityInputRef = useRef();

    const confirmHandler = (event) => {
        event.preventDefault();

        const enteredName = nameInputRef.current.value;
        const enteredStreet = streetInputRef.current.value;
        const enteredPostalCode = postalCodeInputRef.current.value;
        const enteredCity = cityInputRef.current.value;

        const enterNameIsValid = !isEmpty(enteredName);
        const enterStreetIsValid = !isEmpty(enteredStreet);
        const enterPostalCodeIsValid = !isFiveChar(enteredPostalCode);
        const enterCityIsValid = !isEmpty(enteredCity);

        setFormInputcValidity({
            name: enterNameIsValid,
            street: enterStreetIsValid,
            city: enterCityIsValid,
            postalCode: enterPostalCodeIsValid
        });

        const formIsValid =
            enterNameIsValid &&
            enterStreetIsValid &&
            enterPostalCodeIsValid &&
            enterCityIsValid;

        if (!formIsValid) {
            return;
        }
        props.onConfirm({
            name: enteredName,
            street: enteredStreet,
            city: enteredCity,
            postalCode: enteredPostalCode,
        })
    };

    const nameControlClasses = `${classes.control} ${formInputcValidity.name ? '' : classes.invalid}`;
    const streetControlClasses = `${classes.control} ${formInputcValidity.street ? '' : classes.invalid}`;
    const postalCodeClasses = `${classes.control} ${formInputcValidity.postalCode ? '' : classes.invalid}`;
    const cityClasses = `${classes.control} ${formInputcValidity.city ? '' : classes.invalid}`;


    return (
        <form className={classes.form} onSubmit={confirmHandler}>
            <div className={nameControlClasses}>
                <label htmlFor='name'>Your Name</label>
                <input type='text' id='name' ref={nameInputRef} />
                {!formInputcValidity.name && <p> Please Enter a valid name</p>}
            </div>
            <div className={streetControlClasses}>
                <label htmlFor='street'>Street</label>
                <input type='text' id='street' ref={streetInputRef} />
                {!formInputcValidity.street && <p> Please Enter a valid street</p>}
            </div>
            <div className={postalCodeClasses}>
                <label htmlFor='postal'>Postal Code</label>
                <input type='text' id='postal' ref={postalCodeInputRef} />
                {!formInputcValidity.postalCode && <p> Please Enter a valid postal code</p>}
            </div>
            <div className={cityClasses}>
                <label htmlFor='city'>City</label>
                <input type='text' id='city' ref={cityInputRef} />
                {!formInputcValidity.city && <p> Please Enter a valid city</p>}
            </div>
            <div className={classes.actions}>
                <button type='button' onClick={props.onClose}>
                    Cancel
                </button>
                <button className={classes.submit}>Confirm</button>
            </div>
        </form>
    );
};

export default Checkout;