import classes from './AvailableMeals.module.css';
import Card from '../UI/Card';
import MealITem from './MealItem/MealItem';
import { useCallback, useEffect, useState } from 'react';

const AvailableMeals = () => {
    const [foodData, setFoodData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);

    const fetchFoodData = useCallback(async () => {

        try {
            const response = await fetch('https://food-order-app-92f6e-default-rtdb.firebaseio.com/meals.json');
            // console.log(response);
            if (!response.ok) {
                throw Error('Something went wrong !');
            }
            const data = await response.json();
            const loadedData = [];
            for (const key in data) {
                loadedData.push({
                    key: key,
                    id: key,
                    description: data[key].description,
                    name: data[key].name,
                    price: data[key].price,
                })
            }
            setFoodData(loadedData);
        } catch (error) {
            setIsLoading(false);
            setError(error.message);
        }
        setIsLoading(false);
    }, []);

    useEffect(() => {
        fetchFoodData().catch((error) => {
            setIsLoading(false);
            setError(error.message);
        });
    }, []);

    if (isLoading) {
        return <section className={classes.MealsLoading}>
            <p>Loading .......</p>
        </section>
    }

    if (error) {
        return <section className={classes.MealsError}> {error} </section>
    }

    const mealsList = foodData.map(meal => (
        <MealITem
            key={meal.id}
            id={meal.id}
            name={meal.name}
            description={meal.description}
            price={meal.price}
        />));

    let content = <p> Found no data.</p>


    return <section className={classes.meals}>
        <ul>
            <Card >
                {mealsList}
            </Card>
        </ul>
    </section>
};

export default AvailableMeals;

